/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Button, RangeControl } from '@wordpress/components';
import { useInstanceId } from '@wordpress/compose';
import { UnitControl } from '@compatible';

/**
 * @Others dependencies
 */
import classnames from 'classnames';

/**
 * 設定
 */
const UNITS = ['px', 'rem', 'em', '%', 'vw', 'vh'];

/**
 * attributes を数値と単位に分離する
 */
const getUnitNum = (val, defaultUnit) => {
	if (!val) {
		return { num: '', unit: defaultUnit || 'px' };
	}

	// 念のため、明示的に文字列へ変換。
	const valString = val.toString();
	const num = valString.replace(/[^0-9\.]/g, '');
	const unit = valString.replace(/[0-9\.]/g, '');
	return { num: parseFloat(num), unit };
};

/**
 * ArkbUnitNumber
 */
export default ({
	value,
	units,
	min,
	max,
	onChange,
	defaultUnit,
	steps,
	className = '',
	widthRange = false,
	onClear = null,
	// idKey = '',
}) => {
	const _UNITS = units || UNITS;
	const { num, unit } = getUnitNum(value, defaultUnit);
	const instanceId = useInstanceId(UnitControl);
	const inputId = instanceId;
	// const isPx = unit === 'px';
	// text-transform: uppercase;

	let step = '0.1';
	if (steps && steps[unit]) {
		step = steps[unit];
	} else if ('px' === unit) {
		step = '1';
	} else if ('rem' === unit || 'em' === unit) {
		step = '0.05';
	}

	let maxValue;
	if (max !== null && typeof max === 'object') {
		if (undefined !== max[unit]) {
			maxValue = max[unit];
		} else if (undefined !== max.other) {
			maxValue = max.other;
		}
	} else if (undefined !== max) {
		maxValue = max;
	}

	return (
		<div className={classnames('arkb-unitNumber', className, { 'has-clear': !!onClear })}>
			<UnitControl
				id={inputId}
				value={value || defaultUnit || 'px'}
				min={min || 0}
				max={maxValue || undefined}
				step={step}
				units={_UNITS.map((_unit) => {
					return { label: _unit, value: _unit };
				})}
				onBlur={null}
				onChange={(val) => {
					onChange(val);
				}}
				// onUnitChange={() => {}} // onChange が先に走ってるので、何もする必要ない
				// isResetValueOnUnitChange // default値に戻すかどうか？
			/>
			{widthRange && (
				<RangeControl
					value={num}
					onChange={(newNum) => {
						onChange(`${newNum}${unit}`);
					}}
					initialPosition={num}
					withInputField={false}
					step={step}
					min={min || 0}
					max={maxValue || undefined}
				/>
			)}
			{onClear && (
				<Button
					isSmall
					isSecondary
					text={__('Clear')}
					onClick={() => {
						onClear();
					}}
				/>
			)}
		</div>
	);
};
