/**
 * {prop1: value1, prop2: value2} to "prop1:value1;prop2:value2;"
 */
function convertStylesToCssProps(props) {
	let style = '';
	Object.keys(props).forEach((prop) => {
		const value = props[prop];
		if ('' !== value) {
			style += `${prop}:${value};`;
		}
	});
	return style;
}

/**
 * メディアクエリごとのCSSデータをコード化
 */
export default function convertStylesToCssCode(selector, styles) {
	let css = '';
	let props = '';

	if (styles?.all) {
		props = convertStylesToCssProps(styles.all);
		if (props) css += `${selector}{${props}}`;
	}

	// PC ~
	if (styles?.pc) {
		props = convertStylesToCssProps(styles.pc);
		if (props) {
			css += `
			@media (min-width: 1000px) {
				${selector}{${props}}
			}`;
		}
	}

	// ~ pc
	if (styles?.sp) {
		props = convertStylesToCssProps(styles.sp);
		if (props) {
			css += `
			@media not all and (min-width: 1000px) {
				${selector}{${props}}
			}`;
		}
	}

	// Tablet ~
	if (styles?.tab) {
		props = convertStylesToCssProps(styles.tab);
		if (props) {
			css += `
			@media (min-width: 600px) {
				${selector}{${props}}
			}`;
		}
	}

	// mobile
	if (styles?.mb) {
		props = convertStylesToCssProps(styles.mb);
		if (props) {
			css += `
			@media not all and (min-width: 600px) {
				${selector}{${props}}
			}`;
		}
	}
	return css;
}
