import { convertStylesToCssCode } from '@helper';

export function getDynamicStyles({ height, heightPC, heightSP, clientId }) {
	const styles = {
		all: {},
		sp: {},
	};

	if ('custom' === height) {
		styles.all['--arkb-slider-height'] = heightPC;
		styles.sp['--arkb-slider-height'] = heightSP;
	}

	return convertStylesToCssCode(`#block-${clientId} .ark-block-slider`, styles);
}

export function getBlockPropsOnEdit(options) {
	const blockStyle = {};
	const isPC = 1000 <= window.innerWidth ? true : false;
	let isCenter = false;

	if (isPC && 1 < options.slideNumPC) {
		const slideWidth = (100 / options.slideNumPC).toFixed(2);
		blockStyle['--arkb-slide-width'] = `${slideWidth}%`;
		isCenter = options.isCenter;
	} else if (!isPC && 1 < options.slideNumSP) {
		const slideWidth = (100 / options.slideNumSP).toFixed(2);
		blockStyle['--arkb-slide-width'] = `${slideWidth}%`;
		isCenter = options.isCenter;
	}
	const spacePC = options.spacePC;
	if (0 < spacePC) {
		blockStyle['--arkb-slide-space'] = `${spacePC}px`;
	}

	return {
		style: blockStyle,
		'data-is-center': isCenter,
	};
}
