/**
 * @WordPress dependencies
 */
// import { __ } from '@wordpress/i18n';
// import { memo } from '@wordpress/element';
import { TabPanel } from '@wordpress/components';
import { Icon, mobile, tablet, desktop } from '@wordpress/icons';

/**
 * ArkbDeviceTab
 */
export default ({ className, controlPC, controlSP, controlTab, controlMB, isHideTab }) => {
	const deviceTabs = [
		{
			name: 'pc',
			title: (
				<>
					<Icon icon={desktop} />
					<span>PC</span>
				</>
			),
			className: '__pc',
		},
	];
	if (controlTab) {
		deviceTabs.push({
			name: 'tab',
			title: (
				<>
					<Icon icon={tablet} />
					<span>Tab</span>
				</>
			),
			className: '__tab',
		});
	}
	if (controlMB) {
		deviceTabs.push({
			name: 'mb',
			title: (
				<>
					<Icon icon={mobile} />
					<span>Mobile</span>
				</>
			),
			className: '__mb',
		});
	}
	if (controlSP) {
		deviceTabs.push({
			name: 'sp',
			title: (
				<>
					<Icon icon={mobile} />
					<span>SP</span>
				</>
			),
			className: '__sp',
		});
	}

	let tabClass = 'arkb-tabPanel -device';
	if (className) tabClass += ` ${className}`;
	if (isHideTab) tabClass += ' is-hide';

	return (
		<TabPanel
			className={tabClass}
			activeClass='is-active'
			tabs={deviceTabs}
			initialTabName='pc'
		>
			{(tab) => {
				if ('pc' === tab.name) {
					return controlPC;
				} else if ('tab' === tab.name) {
					return controlTab;
				} else if ('mb' === tab.name) {
					return controlMB;
				} else if ('sp' === tab.name) {
					return controlSP;
				}
			}}
		</TabPanel>
	);
};
